export default {
	data() {
		return {
			val:"",
			user:"",
			time:"",
			info:"",
			other:""
		}
	},
	methods: {
		but:function(){
			const vue=this;
			uni.request({
				
				url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/Talk",
				data:{user:vue.user,time:vue.time,speak:vue.val,other:vue.other},
				dataType:"json",
				header:{
					"Content-Type":"application/x-www-form-urlencoded"
				},
				method:"POST",
				success:function(res){
					if(res.data.info=="200"){
						uni.showToast({
							title:"评论成功",
							success:function(){
								setTimeout(()=>{
									uni.hideToast();
									uni.navigateBack({
										animationDuration:1000
									})
								},1500)
							}
						})
					}
					else{
						uni.showLoading({
							title:"评论失败",
							success:function(){
								setTimeout(()=>{
									uni.hideLoading();
									uni.navigateBack({
										animationDuration:1000
									})
								},1500)
							}
						})
					}
				}
			})
		}
	},
	onLoad:function(options){
		console.log(options)
		const vue=this;


















		uni.getStorage({
			key:"id",
			success:function(res){
				vue.user=res.data.user
				vue.time=res.data.time
				vue.info=res.data.info
				vue.other=res.data.other
			}
		})

	}
}
